.container {
  display: grid;
  grid-template-columns: repeat(2, minmax(0, 1fr));
  grid-gap: 20px;
  margin-top: 10px;
}

.container > div:nth-child(2) {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
}

.container > div:nth-child(2) > *:not(:last-child) {
  margin-right: 10px;
}

@media (max-width: 700px) {
  .container {
    grid-template-columns: repeat(1, minmax(0, 1fr));
  }
}
